.Golf-banner{position:relative}.Golf-banner__media-container{position:relative;padding-bottom:var(--aspect-desktop);width:100%;background:linear-gradient(rgba(0, 0, 0, 0), 60%, var(--overlay-opacity))}@media screen and (max-width: 600px){.Golf-banner__media-container{padding-bottom:var(--aspect-mobile)}}.Golf-banner__media-container:empty{display:none}.Golf-banner__media{position:absolute;top:0;left:0;width:100%;height:100%;object-fit:cover;z-index:-1}.Golf-banner__media--mobile{display:none}@media screen and (max-width: 600px){.Golf-banner__media--mobile{display:block}}.Golf-banner__media--desktop{display:block}@media screen and (max-width: 600px){.Golf-banner__media--desktop{display:none}}.Golf-banner__text-container{position:absolute;top:10%;left:10%;height:80%;width:80%;display:flex}.Golf-banner__text-container--wrapper>*{margin:0}.Golf-banner__media-control{display:flex;position:absolute;bottom:5%;left:10%;width:80%;gap:1rem}@media screen and (max-width: 600px){.Golf-banner__media-control{gap:0;left:2rem;width:calc(100% - 4rem)}}.Golf-banner__cta{display:block;margin:0}.Golf-banner [data-state=playing] .Icon--play-outline{display:none}.Golf-banner [data-state=playing] .Icon--pause-outline{display:block}.Golf-banner [data-state=paused] .Icon--play-outline{display:block}.Golf-banner [data-state=paused] .Icon--pause-outline{display:none}.Golf-banner [data-state=muted] .Icon--unmute-outline{display:block}.Golf-banner [data-state=muted] .Icon--mute-outline{display:none}.Golf-banner [data-state=unmuted] .Icon--unmute-outline{display:none}.Golf-banner [data-state=unmuted] .Icon--mute-outline{display:block}.Golf-banner .Icon{width:25px;height:25px}